<template>
  <div>
    <p>&lt;meta&gt; 元素可提供有关页面的元信息（meta-information）,
      比如针对搜索引擎和更新频度的描述和关键词。</p>
    <p>&lt;meta&gt; 标签位于文档的头部，不包含任何内容。标签的属性定义了与文档相关联的名称/值对</p>
    <p class="mb-20">content属性必须有</p>

    <table class="table mb-20">
      <thead>
        <tr><th width="180px">属性</th><th>描述</th></tr>
      </thead>
      <tbody>
        <tr v-for="item in description" :key="item.key">
          <td>{{item.key}}</td><td>{{item.value}}</td>
        </tr>
      </tbody>
    </table>

    <table class="table mb-20">
      <thead>
        <tr><th colspan="2">常用meta标签列表</th></tr>
      </thead>
      <tbody v-for="item in commonList" :key="item.key">
        <tr><td>{{item.key}}</td></tr>
        <tr><td>{{item.value}}</td></tr>
      </tbody>
    </table>

    <table class="table mb-20">
      <thead>
        <tr><th colspan="2">meta标签列表</th></tr>
      </thead>
      <tbody v-for="item in list" :key="item.key">
        <tr><td>{{item.key}}</td></tr>
        <tr v-if="item.value"><td>{{item.value}}</td></tr>
      </tbody>
    </table>

    <table class="table mb-20">
      <thead>
        <tr><th colspan="2">IOS常见meta标签列表</th></tr>
      </thead>
      <tbody v-for="item in IOSlist" :key="item.key">
        <tr><td>{{item.key}}</td></tr>
        <tr><td>{{item.value}}</td></tr>
      </tbody>
    </table>

    <table class="table">
      <thead>
        <tr><th colspan="2">不常用(没什么作用)的meta标签列表</th></tr>
      </thead>
      <tbody v-for="item in notCommonList" :key="item.key">
        <tr><td>{{item.key}}</td></tr>
        <tr><td>{{item.value}}</td></tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        description: [
          {
            key: 'name 属性',
            value: `提供了名称/值对中的名称。HTML 和 XHTML 标签都没有指定任何预先定义的
            <meta> 名称。通常情况下，您可以自由使用对自己和源文档的读者来说富有意义的名称`
          },
          {
            key: 'http-equiv 属性',
            value: `指示服务器在发送实际的文档之前先在要传送给浏览器的MIME 文档头部包含名称/值对。
            当服务器向浏览器发送文档时，会先发送许多名称/值对。虽然有些服务器会发送许多这种名称/值对,
            但是所有服务器都至少要发送一个：content-type:text/html。这将告诉浏览器准备接受一个 HTML 文档。
            使用带有 http-equiv 属性的 <meta> 标签时,服务器将把名称/值对添加到发送给浏览器的内容头部`
          },
          {
            key: 'content 属性',
            value: `提供了名称/值对中的值。该值可以是任何有效的字符串。要和 name 属性或 http-equiv 属性一起使用。`
          },
          {
            key: 'scheme 属性',
            value: '用于指定要用来翻译属性值的方案。此方案应该在由 <head> 标签的 profile 属性指定的概况文件中进行了定义'
          }
        ],

        commonList: [
          {
            key: '<meta charset="utf-8"/>',
            value: '规定 HTML 文档的字符编码。简体中文gb2312,繁体中文 Big5'
          },
          {
            key: '<meta name="keywords" content="your tags" />',
            value: '页面关键词'
          },
          {
            key: '<meta name="description" content="150 words" />',
            value: '页面描述'
          },
          {
            key: `<meta name="viewport" content="width=device-width, initial-scale=1.0,
            minimum-scale=1.0, maximum-scale=1.0, user-scalable=no"/>`,
            value: `强制让文档的宽度与设备的宽度保持1:1，并且文档最大的宽度比例是1.0，且不允许用户点击屏幕放大浏览`
          },
          {
            key: '<meta http-equiv = "X-UA-Compatible" content = "IE=edge,chrome=1" />',
            value: '告诉IE使用最新的引擎渲染网页，chrome=1则可以激活Chrome Frame'
          },
          {
            key: '<meta name="format-detection" content="telephone=no, email=no, adress=no"/>',
            value: '禁止了把数字转化为拨号链接,禁止作为邮箱地址,禁止跳转至地图'
          },
          {
            key: '<meta name="renderer" content="webkit"> ',
            value: '启用360浏览器的极速模式,ie-comp,ie-stand分别代表IE兼容内核(IE6/7), IE标准内核(IE9/IE10/IE11)'
          },
          {
            key: '<meta http-equiv="Access-Control-Allow-Origin" content="*" />',
            value: '表示允许跨域访问。必须在服务器端返回的资源中加入 该头标识, content 的值可以是 URL 或 *'
          },
          {
            key: '<meta name="theme-color" content="#2c3e50">',
            value: '改变 Chrome for Android 地址栏颜色'
          }
        ],

        list: [
          {
            key: '<meta name="robots" content="index,follow"/>',
            value: `搜索引擎抓取, (all：文件将被检索，且页面上的链接可以被查询,
            none:文件将不被检索，且页面上的链接不可以被查询 ,
            index:文件将被检索 , follow：页面上的链接可以被查询 ,
            noindex:文件将不被检索，但页面上的链接可以被查询 ,
            nofollow:文件将不被检索，页面上的链接可以被查询)`
          },
          {
            key: '<meta http-equiv="Cache-Control" content="no-siteapp"/>',
            value: '用百度打开网页可能会对其进行转码(比如贴广告),避免转码可添加如下meta'
          },
          {
            key: '<meta http-equiv="Pragma" content="no-cache"/>',
            value: '禁止浏览器从本地计算机的缓存中访问页面内容:这样设定,访问者将无法脱机浏览'
          },
          {
            key: '<meta http-equiv="Expires" content="Mon,12 May 2001 00:20:00 GMT"/>',
            value: '用于设定网页的到期时间,一旦过期则必须到服务器上重新调用。必须使用GMT时间格式'
          },
          {
            key: '<meta http-equiv="set-cookie" content="Mon,12 May 2001 00:20:00 GMT"/>',
            value: 'cookie设定,如果网页过期,存盘的cookie将被删除。必须使用GMT时间格式'
          },
          {
            key: '<meta name="screen-orientation" content="portrait">',
            value: 'uc强制竖屏'
          },
          {
            key: '<meta name="x5-orientation" content="portrait">',
            value: 'QQ强制竖屏'
          },
          {
            key: '<meta name="full-screen" content="yes">',
            value: ''
          },
          {
            key: '<meta name="x5-fullscreen" content="true">',
            value: 'QQ强制全屏'
          },
          {
            key: '<meta http-equiv="Content-Language"content="zh-cn"/> ',
            value: '当前页面的语言是中文的,请浏览器使用xx编码字符集进行解码显示'
          },
          {
            key: '<meta name="Copyright" content="本页版权归xx所有。All Rights Reserved">',
            value: '标注版权'
          },
          {
            key: '<meta content="always" name="referrer">',
            value: '百度一下'
          }
        ],

        IOSlist: [
          {
            key: '<meta name="apple-mobile-web-app-title" content="标题名称"/>',
            value: '添加到IOS主屏后的标题(iOS 6 新增)'
          },
          {
            key: '<meta name="apple-mobile-web-app-capable" content="yes"/>',
            value: `作用就是删除默认的苹果工具栏和菜单栏,启用 WebApp 全屏模式,
                    需要显示工具栏和菜单栏时,这个行meta就不用加了,默认就是显示`
          },
          {
            key: '<meta name="apple-mobile-web-app-status-bar-style" content="black"/>',
            value: `制定iphone中safari顶端的工具栏颜色(default:白色,black:黑色,black-translucent:灰色半透明)`
          }
        ],

        notCommonList: [
          {
            key: '<meta http-equiv="Refresh" content="n;url=网址"/>',
            value: '定时让网页在指定的时间n内，跳转到你的页面'
          },
          {
            key: '<meta http-equiv="windows-Target" content="_top"/>',
            value: '强制页面在当前窗口中以独立页面显示，可以防止自己的网页被别人当作一个frame页调用'
          },
          {
            key: `<meta http-equiv="Page-Enter" content="revealTrans（duration=10,transition= 50）"/>
              和<meta http-equiv="Page-Exit" content="revealTrans（duration=20，transition=6）"/>`,
            value: '设定进入和离开页面时的特殊效果，这个功能即FrontPage中的"格式/网页过渡",所加的页面不能是一个frame页面'
          },
          {
            key: '<meta name="browsermode" content="application">',
            value: 'UC应用模式'
          },
          {
            key: '<meta name="x5-page-mode" content="app">',
            value: 'QQ应用模式'
          },
          {
            key: '<meta name="msapplication-tap-highlight" content="no">',
            value: 'windows phone 点击无高光'
          },
          {
            key: `<meta name="apple-itunes-app" content="app-id=myAppStoreID,
            affiliate-data=myAffiliateData, app-argument=myURL"/>`,
            value: '添加智能 App 广告条 Smart App Banner(iOS 6+ Safari)'
          },
          {
            key: '<meta http-equiv="refresh" content="0;url=" />',
            value: '数字代表刷新时间。如果加url,则会重定向到指定网页(搜索引擎能够自动检测,也很容易被引擎视作误导而受到惩罚)'
          }
        ]
      }
    }
  }
</script>

<style lang="scss" scoped>
  .table {
    tr:nth-child(2n + 1) {
      background: #efefef;
      color: #42b983;
    }
  }
</style>
